<script>
// import { mapState, mapMutations } from 'vuex'

// import SwpWidget from './widgets/kimai/Swp'
// import CateCard from './widgets/cates/CateCard'
import Dice from './widgets/Dice'
import Case from './widgets/Case'
import Pawn from './widgets/Pawn'
// const axios = require('axios')
class Pops {
  constructor () {
    this.leafs = []
    this._init()
  }

  _init () {
    this.rootElement =
      document.querySelector('#pops-root') || document.createElement('div')
    this.rootElement.setAttribute('id', 'pops-root')
    this.rootElement.style.setProperty('max-width', '100%')
    this.rootElement.style.setProperty('overflow-y', 'auto')
    this.rootElement.style.setProperty('max-height', '100vh')
    // this.rootElement.style.setProperty("pointer-events", "none");
    this.rootElement.classList.add('fxd', '-z-3', '.t', '.e', 'p-4')
    document.body.appendChild(this.rootElement)
  }

  show (message, color = 'rgb(255, 203, 0)') {
    const leaf = document.createElement('div')
    leaf.classList.add('m-b-4', 'p-x-6', 'p-y-5', '-r-2', 'e', 'c-bl')
    leaf.style.setProperty('--c-bl', color)
    // leaf.style.setProperty("pointer-events", "all");
    leaf.innerHTML = message
    this.leafs.unshift(leaf)
    this.rootElement.appendChild(leaf)

    leaf.addEventListener('click', () => {
      leaf.remove()
    })
    return leaf
  }
}
