  },
  components: {
    // SwpWidget,
    Pawn,
    Case,
    Dice
  },
  computed: {
    canStart: function () {
      return (
        Object.values(this.game.players).filter((x) => x.enabled).length >= 2
      )
    },
    playersLength: function () {
      return Object.values(this.game.players).filter((x) => x.enabled).length
    },

    safeCases: function () {
      return [
        this.game.players.A.start,
        this.game.players.B.start,
        this.game.players.C.start,
        this.game.players.D.start,
        this.game.players.A.start + 7,
        this.game.players.B.start + 7,
        this.game.players.C.start + 7,
        this.game.players.D.start + 7
      ]
    },
    pawns: function () {
      return [
        ...this.game.players.A.pawns.list,
        ...this.game.players.B.pawns.list,
        ...this.game.players.C.pawns.list,
        ...this.game.players.D.pawns.list
      ]
    }
  },
  watch: {
    // $route (to, from) {
    // this.$store.commit('config', to.query)
    // },

    '$store.state.theme.brightness': function (to) {
      var root = document.querySelector('.--env')
      localStorage.theme = to
      if (to === 'dark') {
        root.classList.add('--dark')
        root.classList.remove('--light')
      } else {
        root.classList.add('--light')
        root.classList.remove('--dark')
      }
    }
  },
  methods: {
    toggleSpeed () {
      var speeds = [0.1, 0.2, 0.3, 0.5, 0.8, 1, 1.2, 1.3, 1.5, 2, 5]
      var nextIndex = speeds.indexOf(this.game.speed)
      if (speeds[nextIndex + 1]) {
        this.game.speed = speeds[nextIndex + 1]
      } else {
        this.game.speed = speeds[0]
      }
    },
    async start () {
      this.game.state = 'STARTED'
      this.selectPlayer('A')
      if (this.game.players[this.player].bot) {
        await new Promise((resolve) =>
          setTimeout(resolve, this.game.speed * 500)
        )
        this.genRandomResult(true)
      }
    },
    getPlayerRank (player) {
      if (this.game.winers.includes(player)) {
        var index = this.game.winers.indexOf(player)
        if (index === 0) {
          return '👑'
        } else if (index === 1) {
          return '🥈'
        } else if (index === 2) {
          return '🥉'
        } else if (index === 3) {
          return '🏅'
        }
      }
      return false
    },
    getPawnIndexInCase (_player, _pawn, _case) {
      var pawns = []

      for (const __player in this.game.players) {
        for (const __pawn in this.game.players[__player].pawns.list) {
          if (
            this.game.players[__player].pawns.list[__pawn] ===
            this.game.players[_player].pawns.list[_pawn]
          ) {
            pawns.push(__player + __pawn)
          }
        }
      }

      // console.log(pawns, _player, _pawn, _case)

      return {
        index: pawns.indexOf(_player + _pawn),
        pawns: pawns
      }
    },
    checkPlayerIfWin (player) {
      if (this.game.players[this.player].pawns.completed === 4) {
        if (!this.game.winers.includes(player)) {
          this.game.winers.push(player)
        }
        return true
      }
      return false
    },
    rollDice () {
      if (!this.game.players[this.player].bot) {
        this.genRandomResult()
      }
    },
    async genRandomResult (bot = false) {
      if (this.step !== 1) {
        return
      }
      this.randemResult = Math.floor(Math.random() * 6) + 1 // ((Date.now().toString()[12] / 2).toString().split('.')[0]) * 1 + 1
      this.step = 2
      await new Promise((resolve) =>
        setTimeout(resolve, this.game.speed * 200)
      )

      const player = this.game.players[this.player]
      player.pawns.potentials = this.getPlayerPotentials(
        this.player,
        this.randemResult
      )
      const playerPotentialsArreyValues = Object.values(
        player.pawns.potentials
      )
      const playerPotentialsArreyKeys = Object.keys(player.pawns.potentials)

      if (!playerPotentialsArreyKeys.length) {
        return !this.nextPlayer(this.player)
      } else if (playerPotentialsArreyKeys.length === 1 || bot) {
        var selectedRandomPawn = Math.floor(
          Math.random() * playerPotentialsArreyKeys.length
        )
        this.movePawn(
          playerPotentialsArreyValues[selectedRandomPawn],
          this.player,
          player.pawns.list[playerPotentialsArreyValues[selectedRandomPawn]]
        )
      }
    },
    movePawn (id, player, place) {
      place = +place || this.game.players[player].start
      if (
        this.player === player &&
        this.randemResult &&
        place !== this.game.players[player].start * -1 - 3
      ) {
        this.moveDirectly(id, player, place, this.randemResult)
      }
    },
    async moveDirectly (id, player, place, to) {
      if (this.step === 1 || this.isMoving) {
        return
      }
      var nextPlace = this.getNextPlace(player, place, to).next
      var potentialPlace = this.getPawnPotential(id, player, place, nextPlace)
      var potentialPlaceArrey = Object.keys(potentialPlace)
      // console.log(-(this.game.players[player].start - 4))
      // console.log('nextPlace', nextPlace)
      if (
        to === this.OUT_NUMBER ||
        nextPlace === -(this.game.players[player].end - 6)
      ) {
        this.chance++
      }
      if (!potentialPlaceArrey.length) {
        return false
      } else {
        if (potentialPlace[this.game.players[player].start]) {
          await this.animatePawnTo(
            id,
            player,
            place,
            this.game.players[player].start,
            0
          ).then(() => {
            return !this.nextPlayer()
          })
        } else if (potentialPlace[nextPlace]) {
          await this.animatePawnTo(id, player, place, nextPlace).then(() => {
            this.killPawns(id, player, place, nextPlace)
            return !this.nextPlayer(player)
          })
        }
      }
    },
    async animatePawnTo (
      id,
      player,
      place,
      nextPlace,
      result = 1,
      _loop = true
    ) {
      if (this.game.players[player].pawns.completed === 4) {
        window.pops.show('Player ' + player + ' Win!')
        return
      }
      this.isMoving = true
      var nextStepProvider = this.getNextPlace(player, place, result)
      var nextStep = nextStepProvider.next
      this.game.players[player].pawns.list[id] = nextStep

      await new Promise((resolve) =>
        setTimeout(resolve, this.game.speed * 300)
      )
      if (nextStep !== nextPlace && _loop) {
        await this.animatePawnTo(id, player, nextStep, nextPlace)
      }

      this.isMoving = false
      if (!this.checkIfPawnCanMoveTo(id, player, place, nextStep + 1)) {
        this.game.players[player].pawns.completed++
      }
    },
    getPawnElement (player, pawnId) {
      return document.querySelector('[data-pawn-id="' + player + pawnId + '"]')
    },
    getNextPlace (player, place, result) {
      var nextPlace = place + result
      var playerEnd = this.game.players[player].end
      var ended = false
      var restarted = false

      if (nextPlace > 52 && player !== 'B') {
        nextPlace = place + result - 52
        restarted = true
      }
      if (place <= playerEnd && nextPlace > playerEnd) {
        nextPlace = -(playerEnd - (result - (playerEnd - place)))
        ended = true
      }
      return {
        next: nextPlace,
        ended: ended,
        restarted: restarted
      }
    },
    nextPlayer (player = this.player) {
      this.checkPlayerIfWin(player)
      this.chance = Math.max(this.chance - 1, 0)
      if (this.chance === 0) {
        if (player === 'A') {
          this.selectPlayer('B')
        } else if (player === 'B') {
          this.selectPlayer('D')
        } else if (player === 'D') {
          this.selectPlayer('C')
        } else if (player === 'C') {
          this.selectPlayer('A')
        }
      } else {
        this.selectPlayer(player)
      }
      return true
    },
    async selectPlayer (player) {
      this.player = player
      this.chance = 1
      this.step = 1
      this.randemResult = null
      if (
        this.checkPlayerIfWin(player) ||
        !this.game.players[this.player].enabled
      ) {
        if (this.game.winers.length === this.playersLength) {
          this.gameOver()
          return false
        }
        return this.nextPlayer()
      }
      if (this.game.players[this.player].bot) {
        await new Promise((resolve) =>
          setTimeout(resolve, this.game.speed * 500)
        )
        this.genRandomResult(true)
      }
      return true
    },
    getPlayerPotentials (player, result) {
      var playerPotentials = {}
      var playerData = this.game.players[player]

      for (const __pawn in playerData.pawns.list) {
        var place = this.game.players[player].pawns.list[__pawn]
        var nextPlace = this.getNextPlace(player, place, result).next
        var pawnPotential = this.getPawnPotential(
          __pawn,
          player,
          place,
          nextPlace
        )
        playerPotentials = Object.assign(playerPotentials, pawnPotential)
      }

      return playerPotentials
    },
    getPawnPotential (pawnId, player, place, nextPlace) {
      var potentialPlaces = {}
      const _np = this.checkIfPawnCanMoveTo(pawnId, player, place, nextPlace)
      if (
        this.game.players[player].pawns.list[pawnId].toString()[0] === player
      ) {
        if (this.randemResult === this.OUT_NUMBER && _np) {
          potentialPlaces[this.game.players[player].start] = pawnId
        }
      } else {
        if (_np) {
          potentialPlaces[nextPlace] = pawnId
        }
      }
      return potentialPlaces
    },
    checkIfPawnCanMoveTo (pawnId, player, place, nextPlace) {
      var playerData = this.game.players[player]
      if (
        place < playerData.end &&
        nextPlace <= playerData.end &&
        nextPlace <= 0
      ) {
        if (nextPlace > -(playerData.end - 6)) {
          return false
        }
      }
      return true
    },
    killPawns (id, player, place, nextPlace) {
      if (this.safeCases.includes(nextPlace)) {
        return false
      }
      for (const __player in this.game.players) {
        var _player = this.game.players[__player]
        for (const __pawn in _player.pawns.list) {
          var _pawn = this.game.players[__player].pawns.list[__pawn]

          if (__player !== player) {
            if (_pawn === nextPlace) {
              this.game.players[__player].pawns.list[__pawn] =
                __player + __pawn
              this.chance++
            }
          }
        }
      }
    },
    moveStep (id, player, place, i, to) {
      setTimeout(() => {
        var nextPlace = to > 52 ? to - 52 : to
        // var inextPlace = to > 52 ? (to - 52) : to
        this.game.players[player].pawns.list[id] = i
        if (place + i < nextPlace) {
          this.moveDirectly(
            id,
            player,
            place,
            i + 1,
            place + this.randemResult
          )
        }
      }, this.game.speed * 500)
    },
    gameOver () {
      this.game.state = 'OVER'
    },
    reset () {
      delete this.game
      this.game = JSON.parse(JSON.stringify(this.gameBase))
    },
    restart () {
      // setTimeout(() => {
      this.game.state = 'BOOT'
      this.step = 1
      // this.game.players = { ...this.gameBase.players }
      this.reset()
      // this.start()
      // }, 0)
    },
    pause () {
      this.game.state = 'PAUSE'
    },
    async continueGame () {
      this.game.state = 'STARTED'

      if (this.game.players[this.player].bot) {
        await new Promise((resolve) =>
          setTimeout(resolve, this.game.speed * 500)
        )
        this.genRandomResult(true)
      }
    }
  },
  created () {
    // try {
    //   window.screen.orientation.lock('landscape')
    // } catch (error) {
    //   alert(error)
    // }
    this.reset()
    // setInterval(() => {
    //   if (this.activeSideImageId === 4) {
    //     this.activeSideImageId = 0
    //   }
    //   this.activeSideImageId++
    // }, 4000)
  }
}
